<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-date-picker
          v-model="date"
          type="daterange"
          value-format="yyyy-MM-dd"
          range-separator=""
          start-placeholder=""
          end-placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column prop="project_id" label="id" v-if=false>
      </el-table-column>
      <el-table-column sortable prop="projectName" label="" min-width="150">
      </el-table-column>
      <el-table-column sortable prop="submitted_test_director" label="" min-width="150">
      </el-table-column>
      <el-table-column sortable prop="submittedTest_num" label="" min-width="150">
      </el-table-column>
      <el-table-column sortable prop="smokeTesting_pass_num" label="" min-width="150">
      </el-table-column>
      <el-table-column sortable prop="smokeTesting_pass_rate" label="" min-width="150">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { smokeTestingRankReport } from '../../api/submittedTestsApi'
export default {
  data() {
    return {
      date: '',
      listData: ''
    }
  },
  methods: {
    // 
    search() {
      let param = {
        start_date: this.date[0],
        end_date: this.date[1]
      };
      smokeTestingRankReport(param)
        .then(res => {
            this.loading = false;
            if (res.success === false) {
              this.$message({
                type: 'info',
                message: res.msg
              })
            } else {
              this.listData = res.data;
            }
          })
          .catch(err => {
            this.loading = false;
            console.log(err);
            this.$message.error('')
          })
    }
  }
}
</script>

<style scoped>

</style>



